<template lang="pug">
    v-card 
        v-card-text
            v-card-title
                span 机器人自助续费
            v-container
                v-row 
                    v-col(cols="12" sm="12")
                        v-text-field(outlined hide-details disabled v-model="expireData" label="当前过期时间")
                    v-col(cols="12" sm="12")
                        v-text-field(outlined hide-details disabled v-model="trxprice" label="价格(trx/天)")
                    v-col(cols="12" sm="12")
                        v-text-field(outlined hide-details disabled v-model="trxbalance" label="账户trx余额")
                    
                    v-col(cols="12" sm="12")
                        v-text-field(outlined hide-details disabled v-model="trxaddress" label="充值地址")
                    v-col(cols="12" sm="12")
                        v-img(:src="trxaddressqrcode"
                        max-height="250" max-width="250")

                    v-col(cols="12" sm="12")
                        v-text-field(outlined hide-details v-model="days" label="续费天数")
                    
            v-card-actions
                v-row 
                    v-col(cols="12" sm="12" lg="6")
                        v-btn(color="success" dark @click="refresh") 查询用户状态
                    v-col(cols="12" sm="12" lg="6")
                        v-btn( color="success" dark @click="renewal") 确认续费
</template>

<script>
import { get} from 'vuex-pathify'
import * as api from "@/apis/wswebapi"
export default {
    data: () => ({
    trxprice:5,
    trxsunprice: 5000000,
    trxbalance: 0,
    trxsunbalance: 0,
    expiretime:"",
    days:7,
    canrenewal:false,

    }),
    computed:{
        exbotexpiretime:get('user/exbotexpiretime'),
        renewaltime:get('user/renewaltime'),
        trxaddress:get('user/trxaddress'),
        trxaddressqrcode:get('user/trxaddressqrcode'),
        expireData(){
            return new Date(this.exbotexpiretime*1000).toISOString().substr(0, 10)
        },
        username:get('user/username'),
    },
    methods:{
        refresh(){
            api.DBMExpireTime().then(res=>{
                if (res.code==200){
                    this.expiretime=new Date(res.data*1000).toISOString().substr(0, 10);
                    this.trxsunprice=res.trxsunprice;
                    this.trxprice=this.trxsunprice/1000000;
                }else{
                    this.$store.dispatch("msglog/push_msg",{message:res.msg+res.error,type:"error"})
                }
            })
            api.DBMAccountInfo().then(res=>{
                if (res.code==200){
                    this.trxsunbalance=res.data.Balance;
                    this.trxbalance=res.data.Balance/1000000;
                }else{
                    this.$store.dispatch("msglog/push_msg",{message:res.msg+res.error,type:"error"})
                }
            })
        } ,
        renewal(){
            let currencyTime=new Date().getTime();
            if (currencyTime-this.renewaltime<1000*120){
                this.$store.dispatch("msglog/push_msg",{message:"续费操作正在上链，请稍后查询状态即可:",type:"error"})
                return 
            }
            if (Number(this.days)*this.trxsunprice>this.trxsunbalance){
                this.$store.dispatch("msglog/push_msg",{message:"余额不足，至少需要:"+String(Number(this.days)*this.trxsunprice/1000000)+"trx",type:"error"})
                return 
            }
            if (this.days<7){
                this.$store.dispatch("msglog/push_msg",{message:"至少续费7天",type:"error"})
                return 
            }
            if (this.trxsunprice<1000000){
                 this.$store.dispatch("msglog/push_msg",{message:"内部错误，当前状态无法续费",type:"error"})
                return 
            }
            api.UserReqAddExBotExpireTime({days:Number(this.days),username:this.username,TrxSunPrice:this.trxsunprice}).then(res=>{
                if (res.code==200){
                    this.$store.dispatch("msglog/push_msg",{message:res.msg,type:"success"})
                }else{
                    this.$store.dispatch("msglog/push_msg",{message:res.msg+res.error,type:"error"})
                }
            })
            this.$store.dispatch("user/update_renewaltime")
        }
    }
}
</script>